}
.card-event .card-body{
    background-color: #e9c46a;
    overflow: hidden;
}
.card-event p{
    color: white;
}
.card-zoom {
    margin: 0 1em;
    overflow: hidden;
    transition: -webkit-transform .5s ease;
    transition: transform .5s ease;
    transition: transform .5s ease, -webkit-transform .5s ease;
}
.card-zoom:hover {
    -webkit-transform: scale(1.005);
            transform: scale(1.005);
}
.card-zoom--inverted:hover {
    -webkit-transform: scale(.98);
            transform: scale(.98);
}
.card-zoomed {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: .8s ease;
}
.card-zoomed--inverted {
    transition: .5s ease;
}

.card-zoomed:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
@media (max-width: 768px){
    .card-zoomed:hover {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
}
.box{
    overflow: hidden;
    background-color: #424242;
    color: white;
}
.box:before{
    content: '';
    position: absolute;
    top: 0;
    left: -250px;
    width:100px;
    height:100%;
    background: rgba(255, 255, 255, .3);
    -webkit-transform: skew(-40deg );
            transform: skew(-40deg );
    transition: .7s all;
}
.box:hover:before {
    left:200%;
}
.card-image{
    border-radius: 10px;
    border: 3px solid #424242;
}
@media (max-width: 990px){
    .box {
        overflow: hidden;
        background-color: #424242;
        color: white;
    }

    .box:before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100px;
        height: 100%;
        background: rgba(255, 255, 255, .3);
        -webkit-transform: skew(-40deg);
                transform: skew(-40deg);
        transition: .7s all;
    }

    .box:hover:before {
        left: 200%;
    }
}

.card-image-user{
    height: 200px;
    object-fit: cover;
